<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getBuildings"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotesNone"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="buildings" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
            <el-table-column prop="building_id" label="id" width="120" sortable>
			</el-table-column>
			<el-table-column prop="building_name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="layers" label="" width="100"  sortable>
			</el-table-column>
			<el-table-column prop="undergroup_layers" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="units_num" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="building_date" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="city" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="district" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="address" label="" min-width="130" sortable>
			</el-table-column>
            <el-table-column prop="availabel" label="" width="130" :formatter="formatAvailable" sortable>
			</el-table-column>
			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			  <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>  
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="building_name">
					<el-input v-model="editForm.building_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.layers" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.undergroup_layers" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="editForm.units_num" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.building_date"></el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="city">
					<el-input v-model="editForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="district">
					<el-input v-model="editForm.district" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.address"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.available">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="building_name">
					<el-input v-model="addForm.building_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.layers" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.undergroup_layers" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.units_num" :min="0" :max="256"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.building_date"></el-date-picker>
				</el-form-item>
				<el-form-item label="" prop="city">
					<el-input v-model="addForm.city" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="district">
					<el-input v-model="addForm.district" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="addForm.address"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.available">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getBuildingListPage,batchRemoveBuilding,getBuildingList,removeBuilding, editBuilding,addBuilding,getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
                users: [],
                buildings:[],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					building_id: 0,
					building_name: '',
					layers:0,
					undergroup_layers:0,
					units_num:0,
					building_date:'',
					city:'',
					district:'',
					address:'',
					available:1
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					building_id: 0,
					building_name: '',
					layers:0,
					undergroup_layers:0,
					units_num:0,
					building_date:'',
					city:'',
					district:'',
					address:'',
					available:1
				}

			}
		},
		methods: {
			getVotesNone:function(){
			this.filters.name='';
			this.getBuildings();
			},
			//
			formatAvailable: function (row, column) {
				return row.available == 1 ? '' : row.available == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getBuildings();
			},
			//
			getBuildings() {
				let para = {
					page: this.page,
					building_name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getBuildingListPage(para).then((res) => {
					this.total = res.data.total;
					this.buildings = res.data.buildings;
					this.listLoading = false;
				//	NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { building_id: row.building_id };
					removeBuilding(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getBuildings();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					building_id: 0,
					building_name: '',
					layers:0,
					undergroup_layers:0,
					units_num:0,
					building_date:'',
					city:'',
					district:'',
					address:'',
					available:1
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.building_date = (!para.building_date || para.building_date == '') ? '' : util.formatDate.format(new Date(para.building_date), 'yyyy-MM-dd');
							editBuilding(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({//$
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getBuildings();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.building_date = (!para.building_date || para.building_date == '') ? '' : util.formatDate.format(new Date(para.building_date), 'yyyy-MM-dd');
							addBuilding(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getBuildings();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.building_id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveBuilding(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getBuildings();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getBuildings();
		}
	}

</script>

<style scoped>

</style>


