import Mock from 'mockjs';
const Votes = [];
for (let i = 0; i < 86; i++) {
  Votes.push(Mock.mock({
    id: Mock.Random.guid(),//ID
    name: Mock.Random.first(),//
    deadline1:Mock.Random.date('yyyy-MM-dd'),//
    deadline2:Mock.Random.time(),//
    attribute:Mock.Random.first(),//
    vote:{
      title:Mock.Random.cname(),
      radio:Mock.Random.integer(0, 1),
      num:Mock.Random.integer(1,10),
      item:['A','B','C'],//
      voteNum:[100,200,300]//
    }
  }));
}

export {  Votes };


