<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getCommittees"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotesNone"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="committees" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
            <el-table-column prop="id" label="id" width="120" sortable>
			</el-table-column>
			<el-table-column prop="name" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="type" label="" width="120" :formatter="formatType" sortable>
            </el-table-column>
            <el-table-column prop="pid" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="flag" label="" width="120" :formatter="formatFlag" sortable>
            </el-table-column>
            <el-table-column prop="leader" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="detail" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="available" label="" width="120" :formatter="formatAvailable" sortable>
            </el-table-column>
			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			  <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>  
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
                    <el-radio-group v-model="editForm.type">
						<el-radio class="radio" :label="0"></el-radio>
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="2"></el-radio>
                        <el-radio class="radio" :label="3"></el-radio>
                        <el-radio class="radio" :label="4"></el-radio>
                        <el-radio class="radio" :label="5"></el-radio>
                        <el-radio class="radio" :label="6"></el-radio>
                        <el-radio class="radio" :label="7"></el-radio>
					</el-radio-group>
                </el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.pid" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.flag">
						<el-radio class="radio" :label='0'></el-radio>
						<el-radio class="radio" :label='1'></el-radio>
						<el-radio class="radio" :label='2'></el-radio>
					</el-radio-group>
				</el-form-item>
                <el-form-item label="" >
					<el-input v-model="editForm.leader" ></el-input>
				</el-form-item>
                <el-form-item label="" >
					<el-input v-model="editForm.detail" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.available">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
				<el-button @click.native="editFormVisible = false"></el-button>
				
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
                    <el-radio-group v-model="addForm.type">
						<el-radio class="radio" :label="0"></el-radio>
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="2"></el-radio>
                        <el-radio class="radio" :label="3"></el-radio>
                        <el-radio class="radio" :label="4"></el-radio>
                        <el-radio class="radio" :label="5"></el-radio>
                        <el-radio class="radio" :label="6"></el-radio>
                        <el-radio class="radio" :label="7"></el-radio>
					</el-radio-group>
                </el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.pid" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.flag">
						<el-radio class="radio" :label='0'></el-radio>
						<el-radio class="radio" :label='1'></el-radio>
						<el-radio class="radio" :label='2'></el-radio>
					</el-radio-group>
				</el-form-item>
                <el-form-item label="" >
					<el-input v-model="addForm.leader" ></el-input>
				</el-form-item>
                <el-form-item label="" >
					<el-input v-model="addForm.detail" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.available">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import {getCommitteeListPage,getCommitteeList,removeCommittee,batchRemoveCommittee,editCommittee, addCommittee} from '../../api/api';

	export default {
		data() {
			return {
                options: [{
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                }, {
                    value: '',
                    label: ''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                },{
                    value:'',
                    label:''
                }],
                value: '',
                options1:[{
                    value:'0',
                    label:''
                },{
                    value:'1',
                    label:''
                },{
                    value:'2',
                    label:''
                }],
				filters: {
					name: ''
				},
                users: [],
                committees:[],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					type:-1,
					pid:0,
					flag:0,
					leader:'',
					detail:'',
					available:1
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					id: 0,
					name: '',
					type:-1,
					pid:0,
					flag:0,
					leader:'',
					detail:'',
					available:1
				}

			}
		},
		methods: {
			getVotesNone:function(){
			this.filters.name='';
			this.getCommittees();
			},
			//
			formatAvailable: function (row, column) {
				return row.available == 1 ? '' : row.available == 0 ? '' : '';
            },
            formatFlag:function(row,column){
                if(row.flag==0)
                return '';
                else if(row.flag==1)
                return '';
                else if(row.flag==2)
                return '';
                else return '';
            },
            formatType:function(row,column){
                if(row.type==0)
                return '';
                else if(row.type==1)
                return '';
                else if(row.type==2)
                return '';
                else if(row.type==3)
                return '';
                else if(row.type==4)
                return '';
                else if(row.type==5)
                return '';
                else if(row.type==6)
                return '';
                else if(row.type==7)
                return '';
                else return '';
            },
			handleCurrentChange(val) {
				this.page = val;
				this.getCommittees();
			},
			//
			getCommittees() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getCommitteeListPage(para).then((res) => {
					this.total = res.data.total;
					this.committees = res.data.committees;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeCommittee(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCommittees();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					id: 0,
					name: '',
					type:-1,
					pid:0,
					flag:0,
					leader:'',
					detail:'',
					available:1
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// para.building_date = (!para.building_date || para.building_date == '') ? '' : util.formatDate.format(new Date(para.building_date), 'yyyy-MM-dd');
							editCommittee(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({//$
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getCommittees();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							// para.building_date = (!para.building_date || para.building_date == '') ? '' : util.formatDate.format(new Date(para.building_date), 'yyyy-MM-dd');
							addCommittee(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getCommittees();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveCommittee(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getCommittees();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getCommittees();
		}
	}

</script>

<style scoped>

</style>


