<template>
  <div id='launchVote'>
    <el-form ref="form"  :model="form" label-width="80px" style="width:50%" ></br>
      <el-form-item label="" prop="name">
        <el-input type="text" style="width:100%" v-model="LVForm.name" auto-complete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="deadline1">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="LVForm.deadline1" style="width:100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker type="fixed-time" placeholder="" v-model="LVForm.deadline2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="" prop="attribute">
			  <el-input type="text" placeholder="" v-model="LVForm.attribute"></el-input>
			</el-form-item>
      <!---->
      <!-- <el-form-item v-for="i in questions" :key="i"> -->
      <!-- mock.js -->
      <el-form-item>
      <el-card class="box-card" style="width:100%" body-style  >
          <div slot="header" class="clearfix">
            <span style="line-height: 36px;">{{i}}</span>
            <el-button style="float: right;" type="primary" @click="handleFold">{{String_handleFold}}</el-button>
          </div>
          <el-form-item  v-show="willshow" prop="vote.title">
            <a></a>
            <el-input type="text" style="width:100%" v-model="LVForm.vote.title" auto-complete="off" placeholder=""></el-input>
            <el-radio class="radio" v-model="LVForm.vote.radio" label="0"></el-radio>
            <el-radio class="radio" v-model="LVForm.vote.radio" label="1"></el-radio>
          </el-form-item>
          <el-form-item prop="vote.item[o]" v-for="o in LVForm.vote.num" :key="o" class="text item"  v-show="willshow" >{{''+o+''}}
            <el-input  type="textarea"  style="width:100%" v-model="LVForm.vote.item[o]" auto-complete="off" placeholder=""></el-input>
            <!-- <el-button type="primary" style="float:right;"></el-button> -->
            <el-upload
              class="upload-demo"
              action="https://jsonplaceholder.typicode.com/posts/" 
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :file-list="fileList"
              name="this.LVForm.num">
              <el-button size="small" ></el-button>
              <a slot="tip" class="el-upload__tip">jpg/png500kb</a> 
            </el-upload>
            <a href=JavaScript:void(0) v-if="o==LVForm.vote.num" @click="handleDelete" style="float:right"></a>
          </el-form-item>
          <el-form-item>
            <el-button style="width:100%" @click="handleAdd" v-show="willshow" ></el-button>
          </el-form-item>
      </el-card>
      </el-form-item> 
      <el-form-item>
        <!-- <el-button type=""  style='width:49%'  @click='handleAddQuestions'></el-button> -->
        <!-- <el-button type=""  style="width:49%"  @click='handleDeleteQuestions'></el-button> -->
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style='width:100%' @click='handleLaunchVote'></el-button>
      </el-form-item>
    <!---->
    </el-form>
  </div>
</template>

<script>
  import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getVoteListPage, getVoteList, removeVote, batchRemoveVote, editVote,addVote } from '../../api/api';
  export default {
    data(){
      return {
        questions:1,//
        willshow:true,
        String_handleFold:'',
        LVForm:{
          id:'',//id
          name:'',//
          deadline1:'',
          deadline2:'',
          attribute:'',//
          vote:{
            title:'',//
            radio:0,
            num:2,//
            item:[]//
          }
        },
        fileList: []
      }
    },
    methods:{
      handleFold:function(){  
        if(this.willshow==false){
          this.willshow=true;
          this.String_handleFold='';
        }
        else{
          this.willshow=false;
          this.String_handleFold='';
        }
      },
      handleDelete:function(){
        if(this.LVForm.vote.num<=1){
          this.$message('');
          
        }
        else this.LVForm.vote.num-=1;
      },
      handleAdd:function(){
        this.LVForm.vote.num+=1;
      },
      handleRemove:function(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview:function(file) {
        console.log(file);
      },
      handleAddQuestions:function(){
        this.questions+=1;
      },
      handleDeleteQuestions:function(){
        if(this.questions<=1)
          this.$message('');
        else
          this.questions-=1;
      },
      handleLaunchVote:function(){
        this.$refs.LVForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							//NProgress.start();
							let para = Object.assign({}, this.LVForm);
							addVote(para).then((res) => {//
								//NProgress.done();
								this.$message({//$
									message: '',
									type: 'success'
								});
								// this.$refs['editForm'].resetFields();
								// this.editFormVisible = false;
								// this.getVotes();
							});
						});
					}
				});
        this.$router.push({path:'/manage'});
      }
    }
}
</script>

<style>
  .text {
    font-size: 14px;
  }
  .item {
    padding: 18px 0;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  .clearfix:after {
      clear: both
  }

  .box-card {
    width: 480px;
  }
  .show{
    display:none;
  }  
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #20a0ff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>




