<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary"  v-on:click="getCommittee"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotesNone"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="committees" highlight-current-row v-loading="loading"  style="width: 100%;">
            <el-table-column type="index" width="60">
			</el-table-column>
            <el-table-column prop="id" label="id" width="120" sortable>
			</el-table-column>
			<el-table-column prop="name" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="type" label="" width="120" :formatter="formatType" sortable>
            </el-table-column>
            <el-table-column prop="pid" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="flag" label="" width="120" :formatter="formatFlag" sortable>
            </el-table-column>
            <el-table-column prop="leader" label="" width="120" sortable>
            </el-table-column>
            <el-table-column prop="detail" label="" min-width="120" sortable>
            </el-table-column>
            <el-table-column prop="available" label="" width="120" :formatter="formatAvailable" sortable>
            </el-table-column>
		</el-table>

	</section>
</template>
<script>
	import { getCommitteeList } from '../../api/api';
	import NProgress from 'nprogress'
	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				loading: false,
				committees: [
				]
			}
		},
		methods: {
			getVotesNone:function(){
			this.filters.name='';
			this.getCommittee();
			},
			formatAvailable: function (row, column) {
				return row.available == 1 ? '' : row.available == 0 ? '' : '';
            },
            formatFlag:function(row,column){
                if(row.flag==0)
                return '';
                else if(row.flag==1)
                return '';
                else if(row.flag==2)
                return '';
                else return '';
            },
            formatType:function(row,column){
                if(row.type==0)
                return '';
                else if(row.type==1)
                return '';
                else if(row.type==2)
                return '';
                else if(row.type==3)
                return '';
                else if(row.type==4)
                return '';
                else if(row.type==5)
                return '';
                else if(row.type==6)
                return '';
                else if(row.type==7)
                return '';
                else return '';
            },
			//
			getCommittee: function () {
				let para = {
					name: this.filters.name
				};
				this.loading = true;
				//NProgress.start();
				getCommitteeList(para).then((res) => {
					this.committees = res.data.committees;
					this.loading = false;
					//NProgress.done();
				});
			}
		},
		mounted() {
			this.getCommittee();
		}
	};

</script>

<style scoped>

</style>


