import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/userManage/Table.vue'
import user from './views/userManage/user.vue'
import launchVote from './views/vote/launchVote.vue'
import voteList from './views/vote/voteList.vue'
import analyze from './views/vote/analyze.vue'
import manage from './views/vote/manage.vue'
import launchVote_mine from './views/vote/launchVote_mine.vue'
import buildingManege from './views/Building/buildingManage.vue'
import buildingList from './views/Building/buildingList.vue'
import committeeManage from './views/committee/committeeManage.vue'
import committeeList from './views/committee/committeeList.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: '' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/launchVote', component: launchVote, name: '' },
            {path:'/manage',component:manage,name:'',
                children:[
                    {path:'/manage/launchVote_mine',component:launchVote_mine,iconCls: 'el-icon-menu',name:''}
                ]
            },
            { path: '/voteList', component: voteList, name: '' },
            {path:'/analyze',component:analyze,name:''},
        ]
    },
    {
        path:'/',
        component:Home,
        name:'',
        iconCls:'el-icon-menu',
        children:[
            { path:'/buildingManage',component:buildingManege,name:''},
            { path:'/buildingList',component:buildingList,name:''}
        ]
    },
    {
        path:'/',
        component:Home,
        name:'',
        iconCls:'el-icon-menu',
        children:[
            { path:'/committeeManage',component:committeeManage,name:''},
            { path:'/committeeList',component:committeeList,name:''}
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


