<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getBuilding"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotesNone"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="buildings" highlight-current-row v-loading="loading"  style="width: 100%;">
			<el-table-column type="index" width="60">
			</el-table-column>
      <el-table-column prop="building_id" label="id" width="120" sortable>
			</el-table-column>
			<el-table-column prop="building_name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="layers" label="" width="100"  sortable>
			</el-table-column>
			<el-table-column prop="undergroup_layers" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="units_num" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="building_date" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="city" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="district" label="" width="130" sortable>
			</el-table-column>
            <el-table-column prop="address" label="" min-width="130" sortable>
			</el-table-column>
            <el-table-column prop="availabel" label="" width="130" :formatter="formatAvailable" sortable>
			</el-table-column>
		</el-table>

	</section>
</template>
<script>
	import { getBuildingList } from '../../api/api';
	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				loading: false,
				buildings: [
				]
			}
		},
		methods: {
			getVotesNone:function(){
			this.filters.name='';
			this.getBuilding();
			},
			//
			formatAvailable: function (row, column) {
				return row.available == 1 ? '' : row.available == 0 ? '' : '';
			},
			//
			getBuilding: function () {
				let para = {
					building_name: this.filters.name
				};
				this.loading = true;
				//NProgress.start();
				getBuildingList(para).then((res) => {
					this.buildings = res.data.buildings;
					this.loading = false;
					//NProgress.done();
				});
			}
		},
		mounted() {
			this.getBuilding();
		}
	};

</script>

<style scoped>

</style>


