<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotes"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getVotesNone"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="votes" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="id" label="id" min-width="120" sortable>
			</el-table-column>
			<el-table-column prop="name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="deadline1" label="" width="120"  sortable>
			</el-table-column>
			<el-table-column prop="deadline2" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="attribute" label="" width="120" sortable>
			</el-table-column>
			 <el-table-column prop="vote.radio" label="/" width="120" :formatter="formatRadio" sortable> 
			 </el-table-column> 
			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="editForm.deadline1"></el-date-picker>
					<el-time-select  v-model="editForm.deadline2"  :picker-options="{start: '08:30',step: '00:15', end: '18:30' }" placeholder=""></el-time-select>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.attribute"></el-input>
				</el-form-item>
				<el-form-item label="/">
					<el-radio-group v-model="editForm.vote.radio">
						<el-radio class="radio" :label="0"></el-radio>
						<el-radio class="radio" :label="1"></el-radio>
					</el-radio-group>
				</el-form-item>
				</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input v-model="addForm.phone" ></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input v-model="addForm.email" ></el-input>
				</el-form-item>
				<!--   -->
				<el-form-item label="" prop="photo">
					<el-input v-model="addForm.photo" ></el-input>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input v-model="addForm.address" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.regdate"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.available">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
			</el-form>
				
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getVoteListPage, getVoteList, removeVote, batchRemoveVote, editVote,addVote } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				votes: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id:0,
					name:'',
					deadline1:'',
					deadline2:'',
					attribute:'',
					vote:{
						radio:0
					}
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					id: 0,
					name: '',
					sex: -1,
					phone: 0,
					email: '',
					photo: '',
					address:'',
					regdate:'',
					available:1
				}

			}
		},
		methods: {
			//
			formatAvailable: function (row, column) {
				return row.available == 1 ? '' : row.available == 0 ? '' : '';
			},
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			formatRadio:function(row,column){
				return row.vote.radio==0?'':row.vote.radio==1?'':'';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getVotes();
			},
			//
			getVotes() {
				let para = {
					page: this.page,
					attribute: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getVoteListPage(para).then((res) => {
					this.total = res.data.total;
					this.votes = res.data.votes;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeVote(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getVotes();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.$router.push({ path: '/launchVote' });
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.deadline1 = (!para.deadline1 || para.deadline1 == '') ? '' : util.formatDate.format(new Date(para.deadline1), 'yyyy-MM-dd');
							editVote(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({//$
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getVotes();
							});
						});
					}
				});
			},
			getVotesNone:function(){
			this.filters.name='';
			this.getVotes();
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveVote(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getVotes();
					});
				}).catch(() => {

				});
			}
		},
		
		mounted() {
			this.getVotes();
		}
	}

</script>

<style scoped>

</style>


